import React from "react"
import PropTypes from "prop-types"
import Helmet from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"

interface prop {
	description: string;
	lang?: string;
	meta?: Array<any>;
	title: string;
	img?: string;
	keyword?: string;
}

function SEO({description, lang=`kr`, meta=[], title, img=`/background.png`, keyword=`programming, 프로그래밍, coding, 코딩, 개발 블로그, 블로그, blog`}: prop) {
	const { site } = useStaticQuery(
		graphql`
			query {
				site {
					siteMetadata {
						title
						description
						author
					}
				}
			}
		`
	)
	
	const metaDescription = description || site.siteMetadata.description
	  return (
		<Helmet
		  link={[
			{
			  href: `https://fonts.googleapis.com/css?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap`,
			  rel: `stylesheet`
		    },
			{
			  href: `https://fonts.googleapis.com/css?family=Noto+Sans+KR&display=swap`,
			  rel: `stylesheet`
		    }
		  ]}
		  htmlAttributes={{
			lang,
		  }}
		  title={title}
		  meta={[
			{
			  name: `description`,
			  content: metaDescription,
			},
			{
			  property: `og:title`,
			  content: title,
			},
			{
			  property: `og:description`,
			  content: metaDescription,
			},
			{
			  property: `og:type`,
			  content: `website`,
			},
			{
			  name: `og:site_name`,
			  content: `JustKode`,
			},
			{
			  name: `og:image`,
			  content: img,
			},
			{
			  name: `twitter:card`,
			  content: `summary`,
			},
			{
			  name: `twitter:creator`,
			  content: site.siteMetadata.author,
			},
			{
			  name: `twitter:title`,
			  content: title,
			},
			{
			  name: `twitter:description`,
			  content: metaDescription,
			},
			{
			  name: `keywords`,
			  content: keyword,
			},
		  ].concat(meta)}
		>
				<script data-ad-client="ca-pub-4650017493623814" async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script>
		  </Helmet>
	  )
}
export default SEO