import React, { useState, useEffect, useRef } from "react"
import styled from "styled-components"
import { FaGithub, FaInstagram, FaFacebookSquare, FaSearch } from "react-icons/fa"
import { Link } from 'gatsby'

const MainContainer = styled.div`
	border-top: 1px solid #dddddd;
	position: relative;
	width: 100%;
`

const SubContainer = styled.div`
	width: 100%;
	max-width: 1080px;
	position: relative;
	margin: auto;
	color: #90929e;
	height: 120px;
	text-align: center;

	@media (max-width: 1079px) {
		height: 180px;
	}
`

const JustKodeContainer = styled.div`
	line-height: 50px;
	padding: 0 20px;
	font-size: 28px;
	font-weight: 600;
	font-family: Montserrat, Noto Sans KR, Helvetica,Arial, sans-serif;
	display: inline;
	vertical-align: top;

	@media (max-width: 1079px) {
		display: block;
		line-height: normal;
		margin: 10px 0;
	}
`

const ContentContainer = styled.div`
	line-height: 55px;
	padding: 0 20px;
	font-family: Noto Sans KR, Helvetica, Arial, sans-serif;
	display: inline;
	vertical-align: top;
	
	@media (max-width: 1079px) {
		display: block;
		line-height: normal;
		margin: 5px 0;
	}
`

const LinkContainer = styled.div`
	font-size: 32px;
	display: inline-block;
	a {
		text-decoration: none;
		color: #90929e;
	}
	svg {
		padding: 8px;
	}

	@media (max-width: 1079px) {
		display: block;
	}
`

const SearchContainer = styled.div`
	
`

const SearchBarContainer = styled.input`
	width: 200px;
	border: 3px solid #90929e;
	border-right: none;
	padding: 5px;
	height: 20px;
	border-radius: 5px 0 0 5px;
	outline: none;
	vertical-align: top;
	color: #90929e;
`

const SearchButtonContainer = styled.button`
	width: 40px;
	height: 36px;
	border: 1px solid #90929e;
	background: #90929e;
	text-align: center;
	color: #fff;
	border-radius: 0 5px 5px 0;
	cursor: pointer;
	font-size: 20px;
`

const BottomLinkContainer = styled.div`
	padding: 20px 0;
	a {
		text-decoration: none;
		color: #90929e;
		font-weight: bold;
	}
`

export default function Footer() {
	const [text, setText] = useState('')
	
	function Search() {
		if (text == "") {
			alert("검색어를 입력 해 주세요.")
		} else {
			open('https://www.google.com/search?q=site%3Ajustkode.kr+' + text)
		}
	}
	
	return (
		<MainContainer>
			<SubContainer>
				<JustKodeContainer>justkode</JustKodeContainer>
				<ContentContainer>© <strong>justkode</strong> All Right Reserved.</ContentContainer>
				<LinkContainer>
					<a href="https://github.com/JustKode/"><FaGithub /></a>
					<a href="https://instagram.com/__o.cean/"><FaInstagram /></a>
				</LinkContainer>
				<SearchContainer>
					<SearchBarContainer
						placeholder="검색어"
						onChange={e => setText(e.target.value)}
						onKeyDown={e => {
							if (e.key == "Enter") {Search()}
						}}
						value={text}/>
					<SearchButtonContainer onClick={Search}> 
						<FaSearch />
					</SearchButtonContainer>
				</SearchContainer>
				<BottomLinkContainer>
					<Link to='/archive'>Archive</Link>
				</BottomLinkContainer>
			</SubContainer>
		</MainContainer>
	)
}