// Scroll Event

export default function Header() {
	const [ checked, setChecked ] = useState(false)
	const [ scrolled, setScrolled ] = useState(false)
	
	useEffect(() => {
		const handleScroll = () => {
			const currentScrollY = window.scrollY
			if (currentScrollY > 0) {
				setScrolled(true)
			} else {
				setScrolled(false)
			}
		}
		
		window.addEventListener("scroll", handleScroll)
		
		return () => window.removeEventListener("scroll", handleScroll)
	}, [scrolled])
	
	const toggle = () => {
		setChecked(!checked)
	}
	
	const { allMarkdownRemark: {group} } = useStaticQuery(graphql`
		query {
		  allMarkdownRemark(filter: {frontmatter: {tags: {nin: ["Archive"]}}}) {
			group(field: {frontmatter: {tags: SELECT}}) {
			  fieldValue
			  totalCount
			}
		  }
		}
	`)
	group.sort(function (a, b) {
	  if (a.totalCount > b.totalCount) {
			return -1;
		}
	  else if (a.totalCount < b.totalCount) {
			return 1;
		}
	  return 0;
	})
	
	const groups = group.map( x => <li key={x.fieldValue}><Link to={`/${x.fieldValue.toLowerCase()}`}><Board>{x.fieldValue}</Board> <Count>{x.totalCount}</Count></Link></li>)
	
	return (
		<MainContainer className={`${scrolled && 'scrolled'}`}>
			<SubContainer>
				<Link to='/'><Title>justkode</Title></Link>
				<Menu className={`${checked && 'checked'}`}>
					<li><Link to='/info'>info</Link></li>
					<li><Link to='/proj'>proj</Link></li>
					<li className='post'>
						<Link to='/post'>post</Link>
						<ul>
							{groups}
						</ul>
					</li>
					
				</Menu>
				<IoIosMenu onClick={toggle}/>
			</SubContainer>
		</MainContainer>
	)
}